  background-color: #81b3d2;
}

.social__link {
  text-decoration: none;
  display: block;
  cursor: pointer;
}
.social__link[href]:hover {
  background-color: #669ec0;
}
.social__link[href]:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}

.video-presentation__wrapper {
  box-sizing: border-box;
  width: 320px;
  margin-left: auto;
  margin-right: auto;
}

.video-presentation__title,
.video-presentation__description {
  margin-top: 0;
  margin-bottom: 0;
}

.video-presentation__title {
  padding: 17px 27px 32px;
}

.video-presentation__description {
  padding: 0 27px 38px;
}

.video-presentation__video-file {
  max-width: 100%;
